FlowRouter.route('/blog/:postId', {
	name: 'blog',
	action: function (params, queryParams) {
		console.log("Yeah! We are on the post:", params.postId + ' name: ' + FlowRouter.getRouteName());
		Session.set('template', FlowRouter.getRouteName());
	}
});
FlowRouter.route('/dog/:what', {
	name: 'dog',
	action: function (params, queryParams) {
		console.log('Now we are dog: ' + params.what + ' name: ' + FlowRouter.getRouteName());
		Session.set('template', FlowRouter.getRouteName());
	}
});
FlowRouter.route('/fog/:what', {
	name: 'fog',
	action: function (params, queryParams) {
		console.log('Now we are fog: ' + params.what + ' name: ' + FlowRouter.getRouteName());
		Session.set('template', FlowRouter.getRouteName());
	}
});
FlowRouter.route('/', {
	name: 'base',
	action: function (params, queryParams) {
		console.log('Now we are base: ' + params.what + ' name: ' + FlowRouter.getRouteName());
		Session.set('template', 'dog');
	}
});